.main{
    width: 100%;
    min-height: 45vh;
    /* background-color: rgb(255, 213, 178); */
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5%;
    padding: 0px 10px;
}
.imgs{
    width: 75px;
    height: 240px;
    border-radius: 15px;
    transition: width .9s ease-in-out;
    position: relative;
    cursor: pointer;
}
#expanded{
    background-image: url("https://rukminim2.flixcart.com/fk-p-flap/1600/270/image/82d07a5ce43db9fc.jpg?q=20");
    background-size: cover;
    width: 76%;
}
.main :nth-child(2){
    background-image: url("https://rukminim2.flixcart.com/fk-p-flap/1600/270/image/0d1366a919fff51b.jpg?q=20");
    background-size: cover;
}
.main :nth-child(3){
    background-image: url("https://rukminim2.flixcart.com/fk-p-flap/1600/270/image/e8afba7feffa58f9.jpg?q=20");
    background-size: cover;
}
.main :nth-child(4){
    background-image: url("https://rukminim2.flixcart.com/fk-p-flap/1600/270/image/a10d78bd3ae5316b.jpg?q=20");
    background-size: cover;
}
.main :nth-child(5){
    background-image: url("https://rukminim2.flixcart.com/fk-p-flap/1600/270/image/352e6f0f8034fab5.jpg?q=20");
    background-size: cover;
}
.main :nth-child(n){
    background-repeat: no-repeat;
    background-size: cover;
    background-size: inherit;
}

#expanded Link{
    opacity: 1;
    bottom: 30px;
    top: 40px;
}

Link h2{
    color: black;
    position: absolute;
    bottom: 0;
    left: 30px;
    opacity: 0;
    transition: all .9s .2s ease-in-out;
}